<template>
    <div>
      <h2><i class="fa fa-tags"></i>    </h2>
      <div ref="chart" style="width: 99%; height: 180px;"></div>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  import * as echarts from 'echarts';
  
  export default {
    data() {
      return {
        chart: null,
        config: {
          backgroundColor: 'transparent',
          // title: {
          //   text: '',
          //   left: 'center',
          //   top: '12',
          //   bottom:'20',
          //   textStyle: {
          //     color: '#fff',
          //     fontSize: 18,
          //     fontWeight: '700'
          //   }
          // },
          tooltip: {
            trigger: 'item',
            formatter: '{b}: {c} ({d}%)'
          },
          legend: {
            show: false
          },
          series: [
            {
              name: '',
              type: 'pie',
              radius: ['50%', '70%'],
              center: ['50%', '50%'], // 
              top:'10',
              avoidLabelOverlap: false,
              label: {
                show: true, // 
                position: 'outside', // 
                formatter: '{b}: {d}%', //  + 
                color: '#fff', // 
                fontSize: 12
              },
              labelLine: {
                show: true, // 
                length: 10, // 
                length2: 20, // 
                lineStyle: {
                  color: '#fff' // 
                }
              },
              emphasis: {
                label: {
                  show: true,
                  fontSize: 14,
                  fontWeight: 'bold'
                }
              },
              data: [],
              itemStyle: {
                borderColor: '#0A2E5D',
                borderWidth: 1,
                shadowBlur: 20,
                shadowColor: 'rgba(0, 255, 255, 0.8)' // 
              },
              color: [
                '#1f78b4', // 
                '#6baed6', // 
                '#9ecae1', // 
                '#c6dbef', // 
                '#3182bd', // 2
                '#6baed6', // 2
                '#9ecae1', // 2
                '#c6dbef', // 2
                '#08519c', // 3
                '#2171b5'  // 3
              ]
            },
            {
              type: 'pie',
              radius: ['50%', '70%'],
              center: ['50%', '50%'], // 
              silent: true,
              top:'10',
              label: { show: false },
              data: [{ value: 1, itemStyle: { color: 'rgba(255,255,255,0.1)' } }],
              itemStyle: {
                color: 'transparent',
                borderWidth: 2,
                borderColor: 'rgba(255,255,255,0.2)'
              }
            }
          ]
        }
      };
    },
    mounted() {
      this.initChart();
      this.fetchData();
    },
    methods: {
      initChart() {
        this.chart = echarts.init(this.$refs.chart);
        this.chart.setOption(this.config);
      },
      async fetchData() {
        try {
          const response = await axios.get('/main/all_hot_class');
          if (response.data.code === 0) {
            const classCount = response.data.data;
            const data = Object.entries(classCount).map(([category, count]) => ({
              value: count,
              name: category
            }));
            this.config.series[0].data = data;
            this.chart.setOption(this.config);
          } else {
            console.error(':', response.data.message);
          }
        } catch (error) {
          console.error(':', error);
        }
      }
    }
  };
  </script>
  
  <style scoped>
  h2 {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 1px; /*  */
    margin-left: 150px;
}
  /*  */
  </style>