<template>
  <div class="sentiment_ana">
    <div class="sentiment_ana_title"></div>
    <div class="sentiment_ana_contents">
      <div class="sentiment_chart" ref="sentimentChart" style="height: 265px;"></div>
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts';

export default {
  name: "sentiment_ana",
  data() {
    return {
      sentimentData: null
    };
  },
  methods: {
    // 
    getComments() {
      let query = this.$route.query;
      //  URL 
      const encodedUrl = encodeURIComponent(query.url);
      this.$axios
        .get("comment/mood_tb", {
          params: {
            tag_task_id: query.tag_task_id,
            url: encodedUrl,
          }
        })
        .then((res) => {
          console.log(res);
          // 
          this.sentimentData = res.data.data.data.sentiment_by_date;

          // 
          this.drawSentimentChart();
        })
        .catch(error => {
          console.error(":", error);
        });
    },
    drawSentimentChart() {
      if (!this.sentimentData) return;

      // 
      const dates = Object.keys(this.sentimentData).sort();

      // 
      const sentimentTypes = new Set();
      for (const date in this.sentimentData) {
        for (const sentiment in this.sentimentData[date]) {
          sentimentTypes.add(sentiment);
        }
      }

      // 
      const seriesData = [];
      sentimentTypes.forEach(sentiment => {
        const data = [];
        dates.forEach(date => {
          data.push(this.sentimentData[date][sentiment] || 0);
        });

        let areaColor;
        let chineseName;
        switch (sentiment) {
          case 'positive':
            chineseName = '';
            areaColor = 'rgba(54, 162, 235, 0.3)'; // 
            break;
          case 'neutral':
            chineseName = '';
            areaColor = 'rgba(255, 205, 86, 0.3)'; // 
            break;
          case 'negative':
            chineseName = '';
            areaColor = 'rgba(255, 99, 132, 0.3)'; // 
            break;
          default:
            areaColor = 'rgba(153, 153, 153, 0.3)'; // 
        }

        seriesData.push({
          name: chineseName,
          type: 'line',
          data: data,
          areaStyle: {
            color: areaColor
          },
          lineStyle: {
            color: areaColor.replace('0.3', '1') // 
          }
        });
      });

      //  echarts 
      const chart = echarts.init(this.$refs.sentimentChart);

      // 
      const option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: Array.from(sentimentTypes).map(sentiment => {
            switch (sentiment) {
              case 'positive':
                return '';
              case 'neutral':
                return '';
              case 'negative':
                return '';
              default:
                return '';
            }
          }),
          textStyle: {
            color: '#fff' // 
          }
        },
        xAxis: {
          type: 'category',
          data: dates,
          axisLabel: {
            color: 'white' // 
          }
        },
        yAxis: {
          type: 'value',
          min: 0,
          axisLabel: {
            formatter: function (value) {
              return Math.round(value); // 
            },
            color: 'white' // 
          }
        },
        series: seriesData
      };

      // 
      chart.setOption(option);
    }
  },
  mounted() {
    this.getComments();
  }
};
</script>

<style scoped>
.sentiment_ana {
  width: 100%;
  height: 100%;
}

.sentiment_ana_title {
  color: #ffffff;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 6px;
  margin-left: 10px;
}

.sentiment_ana_contents {
  width: 100%;
  height: calc(100% - 50px);
}

.sentiment_chart {
  width: 100%;
  height: 100%;
}
</style>