<template>
  <div class="container">
    <nav>
      <ul class="mcd-menu">
        <!--  -->
        <li>
          <a @click="ToHome" :class="{ active: activePath === '/home' }">
            <i class="fa fa-arrow-left"></i>
            <strong></strong>
          </a>
        </li>
        <!--  -->
        <li>
          <a @click="ToMain" :class="{ active: activePath === '/mainscreen' }">
            <i class="fa fa-fire"></i>
            <strong></strong>
          </a>
        </li>
        <!--  -->
        <li>
          <a @click="ToHot" :class="{ active: activePath === '/hot' }">
            <i class="fa fa-fire"></i>
            <strong></strong>
          </a>
        </li>
        <!--  -->
        <li>
          <a @click="ToAllView" :class="{ active: activePath === '/all_view' }">
            <i class="fa fa-search"></i>
            <strong></strong>
          </a>
        </li>
        <!--  -->
        <li>
          <a @click="ToRumor" :class="{ active: activePath === '/Rumor' }">
            <i class="fa fa-bullhorn"></i>
            <strong></strong>
          </a>
        </li>

        <!--  -->
        <li>
          <a @click="toggleSubMenu" :class="{ active: subMenuOpen }">
            <i class="fa fa-file-text-o"></i>
            <strong></strong>
            <!-- <i class="fa fa-sort-down"></i> -->
          </a>
          <ul v-if="subMenuOpen" class="sub-menu-row">
            <!--  -->
            <li><a @click="ToWB" :class="{ active: activePath === '/wb' }"><i class="fa fa-weibo"></i><small></small></a></li>
            <!--  -->
            <li><a @click="ToWX" :class="{ active: activePath === '/wx' }"><i class="fa fa-weixin"></i><small></small></a></li>
            <!--  -->
            <li><a @click="ToRM" :class="{ active: activePath === '/rm' }"><i class="fa fa-globe"></i><small></small></a></li>
            <!--  -->
            <li><a @click="ToTB" :class="{ active: activePath === '/tb' }"><i class="fa fa-comments-o"></i><small></small></a></li>
          </ul>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  name: "side_border",
  data() {
    return {
      subMenuOpen: false,
      activePath: this.$route.path
    };
  },
  methods: {
    ToHome() {
      if (this.activePath!== "/home") {
        this.$router.push({
          path: "/home"
        });
        this.activePath = "/home";
      }
    },
    ToMain() {
      if (this.activePath!== "/mainscreen") {
        this.$router.push({
          path: "/mainscreen"
        });
        this.activePath = "/mainscreen";
      }
    },
    ToAllView() {
      if (this.activePath!== "/all_view") {
        this.$router.replace({
          path: "/all_view"
        });
        this.activePath = "/all_view";
      }
    },
    ToRumor() {
      if (this.activePath!== "/Rumor") {
        this.$router.replace({
          path: "/Rumor"
        });
        this.activePath = "/all_view";
      }
    },
    ToHot() {
      if (this.activePath!== "/hot") {
        this.$router.push({
          path: "/hot"
        });
        this.activePath = "/hot";
      }
    },
    ToWB() {
      if (this.activePath!== "/wb") {
        this.$router.push({
          path: "/wb"
        });
        this.activePath = "/wb";
      }
      this.subMenuOpen = false;
    },
    ToWX() {
      if (this.activePath!== "/wx") {
        this.$router.push({
          path: "/wx"
        });
        this.activePath = "/wx";
      }
      this.subMenuOpen = false;
    },
    ToRM() {
      if (this.activePath!== "/rm") {
        this.$router.push({
          path: "/rm"
        });
        this.activePath = "/rm";
      }
      this.subMenuOpen = false;
    },
    ToTB() {
      if (this.activePath!== "/tb") {
        this.$router.push({
          path: "/tb"
        });
        this.activePath = "/tb";
      }
      this.subMenuOpen = false;
    },
    toggleSubMenu() {
      this.subMenuOpen =!this.subMenuOpen;
    }
  }
};
</script>

<style scoped>
html, body {
  height: 100%; /*  html  body  100% */
  margin: 0;
  padding: 0;
}

.container {
  width: 150px; /*  */
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: #070d7b00;
  color: white;
  box-sizing: border-box;
  display: flex;
  flex-direction: column; /*  */
}
.mcd-menu {
  flex: 1;
  height: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
  background: #070d7b00; /*  */
  font-family: 'Open Sans', Arial, sans-serif;
}

.mcd-menu li {
  position: relative;
}

.mcd-menu li a {
  display: flex; /*  flex  */
  align-items: center; /*  */
  text-decoration: none;
  padding: 15px 25px;
  color: white;
  transition: background-color 0.3s;
}

.mcd-menu li a i {
  margin-right: 5px; /*  */
}

.sub-menu-row {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  padding: 0;
  justify-content: center; /*  */
  align-items: center; /*  */
}

.sub-menu-row li {
  margin: 0 18px; /*  */
}

.sub-menu-row li a {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.sub-menu-row li a i {
  margin-right: 8px; /*  */
}

.mcd-menu li a:hover {
  color: aqua;
}

.mcd-menu li a.active {
  box-shadow: 0 0 10px rgba(187, 197, 209, 0.8); /*  */
  color: aqua;
}

.mcd-menu li a i {
  display: block;
  font-size: 24px;
  margin-bottom: 5px;
  text-align: center;
}

.mcd-menu li a strong {
  display: block;
  text-align: center;
}
.mcd-menu li a small {
  display:block;
  text-align: center;
}

.mcd-menu li ul {
  display: none;
  position: absolute;
  left: 0; /*  0 */
  top: 100%; /*  */
  background: #192d5200; /*  */
  list-style: none;
  padding: 0;
  margin: 0;
  width: 150px; /*  */
}

.mcd-menu li ul li a {
  padding: 10px 15px;
  
}

.mcd-menu li:hover > ul {
  display: block;
}
</style>