<template>
    <div class="main-container">
      <div class="side">
        <SideBorder />
      </div>
    <div class="content">
      <div class="container-wrapper">
        <div class="wb-container">
            <h1></h1>
            <button class="tech-button" @click="fetchHotSearchWb"></button>
            
            <!--  -->
            <ul class="search-list">
            <li v-for="(item, index) in hotSearchListWb" :key="index" class="search-item">
                <div class="hot-search-details">
                <div class="order-box">
                    <span :class="['order', { 'highlight': index < 3 }]">
                        No.{{ item.seq }}</span>
                </div>
                </div>
    
                <div class="title">
                <a :href="item.url" target="_blank">{{ formatTitle(item.title) }}</a>
                <span v-if="item.label" class="label">{{ item.label }}</span>
                </div>
            </li>
            </ul>
        </div>

        <div class="baidu-container">
            <h1></h1>
            <button class="tech-button" @click="fetchHotSearchBaidu"></button>
            
            <!--  -->
            <ul class="search-list">
            <li v-for="(item, index) in hotSearchListBaidu" :key="index" class="search-item">
                <div class="hot-search-details">
                <div class="order-box">
                    <span :class="['order', { 'highlight': index < 3 }]"> No.{{ item.seq }}</span>
                </div>
                </div>
    
                <div class="title">
                <a :href="item.url" target="_blank">{{ formatTitle(item.title) }}</a>
                <span v-if="item.label" class="label-baidu">{{ item.hot_index }}</span>
                </div>
            </li>
            </ul>
        </div>

        <div class="tieba-container">
            <h1></h1>
            <button class="tech-button" @click="fetchHotSearchTieba"></button>
            
            <!--  -->
            <ul class="search-list">
            <li v-for="(item, index) in hotSearchListTieba" :key="index" class="search-item">
                <div class="hot-search-details">
                <div class="order-box">
                    <span :class="['order', { 'highlight': index < 3 }]"> No.{{ item.seq }}</span>
                </div>
                </div>
    
                <div class="title">
                <a :href="item.url" target="_blank">{{ formatTitle(item.title) }}</a>
                <span v-if="item.label" class="label-tieba">{{ item.hot }}</span>
                </div>
            </li>
            </ul>
        </div>
      </div>
    

      <div class="container-analyze">
        <div class="sentiment">
            <div id="sentimentChart" style="height: 380px;"></div>
        </div> 
        <div class="wordcloud">
            <div id="wordcloudChart" style="height: 380px;"></div>
        </div>
      </div>
    </div>
    </div>
  </template>
  
  <script>
 import axios from 'axios'; //  axios
 import * as echarts from 'echarts'; //  ECharts
  import SideBorder from "../components/Side_border";
  
  export default {
    data() {
      return {
        hotSearchListWb: [], // 
        hotSearchListTieba: [], // 
        hotSearchListBaidu: [], // 
        sentimentData: { // 
            wb: { positive: 0, negative: 0, neutral: 0 },
            tieba: { positive: 0, negative: 0, neutral: 0 },
            baidu: { positive: 0, negative: 0, neutral: 0 }
        },
        maxWordCount: 120 ,// 
        wordcloudData: [] // 
      };
    },
    components: {
      SideBorder,
    },
    mounted() {
        this.fetchHotSearchWb(); // 
        this.fetchHotSearchTieba(); // 
        this.fetchHotSearchBaidu(); // 
        this.drawSentimentChart(); // 
        this.fetchWordCloudData(); // 
    },
    methods: {
      formatTitle(title) {
      if (title.length > 13) {
        return title.slice(0, 13) + '...';
      }
      return title;
    },

  async fetchHotSearchWb() {
    this.sentimentData.wb = { positive: 0, negative: 0, neutral: 0 };
    try {
      const response = await this.$axios.get('/weibo_hot'); // 
      if (response.data.code === 0) {
        this.hotSearchListWb = response.data.data; // 
        this.analyzeSentiment('wb', this.hotSearchListWb); // 
      } else {
        alert('');
      }
    } catch (error) {
      alert('' + error.message);
    }
  },

  async fetchHotSearchTieba() {
    this.sentimentData.tieba = { positive: 0, negative: 0, neutral: 0 };
    try {
      const response = await this.$axios.get('/tieba_hot'); // 
      if (response.data.code === 0) {
        this.hotSearchListTieba = response.data.data; // 
        this.analyzeSentiment('tieba', this.hotSearchListTieba); // 
      } else {
        alert('');
      }
    } catch (error) {
      alert('' + error.message);
    }
  },

  async fetchHotSearchBaidu() {
    this.sentimentData.baidu = { positive: 0, negative: 0, neutral: 0 };
    try {
      const response = await this.$axios.get('/baidu_hot'); // 
      if (response.data.code === 0) {
        this.hotSearchListBaidu = response.data.data; // 
        this.analyzeSentiment('baidu', this.hotSearchListBaidu); // 
      } else {
        alert('');
      }
    } catch (error) {
      alert('' + error.message);
    }
  },
// 
analyzeSentiment(app, hotSearchList) {
      hotSearchList.forEach(item => {
        const sentiment = item.sentiment; //  (01)
        if (sentiment >= 0.7) {
          this.sentimentData[app].positive += 1;
        } else if (sentiment <= 0.3) {
          this.sentimentData[app].negative += 1;
        } else {
          this.sentimentData[app].neutral += 1;
        }
      });
      this.drawSentimentChart(); // 
    },

    //  ECharts 
    drawSentimentChart() {
      const sentimentChart = echarts.init(document.getElementById('sentimentChart'));

    const option = {
    title: {
        text: '',
        left: 'center',
        textStyle: {
        color: '#FFFFFF', // 
        fontSize: 18,
        fontWeight: 'bold'
        },
        padding: [5, 0, 10, 0] // 
    },
    tooltip: {
        trigger: 'axis',
        axisPointer: {
        type: 'shadow'
        }
    },
    legend: {
        data: ['', '', ''],
        top: 'top',
        textStyle: {
        color: '#FFFFFF', // 
        fontSize: 14
        },
        padding: [30, 0, 20, 0] // 
    },
    xAxis: {
        type: 'category',
        data: ['', '', ''],
        axisLabel: {
        textStyle: {
            color: '#FFFFFF', // 
        }
        },
        axisLine: {
        lineStyle: {
            color: '#FFFFFF' // 
        }
        }
    },
    yAxis: {
        type: 'value',
        min: 0,
        axisLabel: {
        textStyle: {
            color: '#FFFFFF', // 
        }
        },
        axisLine: {
        lineStyle: {
            color: '#FFFFFF' // 
        }
        }
    },
    series: [
        {
        name: '',
        type: 'bar',
        data: [
            this.sentimentData.wb.positive,
            this.sentimentData.baidu.positive,
            this.sentimentData.tieba.positive 
        ],
        itemStyle: {
            color: '#1E90FF' 
        }
        },
        {
        name: '',
        type: 'bar',
        data: [
            this.sentimentData.wb.negative,
            this.sentimentData.baidu.negative,
            this.sentimentData.tieba.negative
        ],
        itemStyle: {
            color: '#4682B4' // 
        }
        },
        {
        name: '',
        type: 'bar',
        data: [
            this.sentimentData.wb.neutral,
            this.sentimentData.baidu.neutral,
            this.sentimentData.tieba.neutral
        ],
        itemStyle: {
            color: '#5F9EA0' 
        }
        }
    ]
    };

    sentimentChart.setOption(option);
    },

    // 
    async fetchWordCloudData() {
      try {
        const response = await this.$axios.get('/hot_wordcloud'); // 
        if (response.data.code === 0) {
          this.wordcloudData = response.data.data.wordcloud_data; // 
          this.drawWordCloud(); // 
        } else {
          alert('');
        }
      } catch (error) {
        alert('' + error.message);
      }
    },

      // 
        drawWordCloud() {
          //  maxWordCount 
        const limitedData = this.wordcloudData.slice(0, this.maxWordCount);

        const data = limitedData; // 

        // 
        const chart = echarts.init(document.getElementById('wordcloudChart'));
        const option = {
            tooltip: {
            show: true,
            formatter: function (params) {
                return params.name + ": " + params.value;
            }
            },
            series: [{
            type: 'wordCloud',
            gridSize: 8, // 
            sizeRange: [15, 30], // 
            rotationRange: [0, 0], // 
            shape: 'circle', //  'circle', 'cardioid', 'diamond', 'triangle-forward', 'triangle', 'pentagon', 'star'

            
            width: '70%',
            height: '70%',
            right: 'center',
            bottom: 'center',
            textStyle: {
                color: function () {
                // 
                return "rgb(" +
                    [
                    Math.round(Math.random() * 55 + 200), // 200-255
                    Math.round(Math.random() * 55 + 200), // 200-255
                    Math.round(Math.random() * 55 + 200), // 200-255
                    ].join(",") + ")";
                }
            },
            // 
            wordCloud: {
                rotationRange: [-45, 90], // 
                width: '100%',
                height: '100%',
                
                // 
                sizeRange: [30, 60]
            },
            data: data.map(item => ({
                name: item.name,
                value: item.value
            }))
            }]
        };
        chart.setOption(option);
    }
  }
};
</script>

  
