<template>
  <div class="relation_graph" style="height: 100%;">
    <div class="relation_graph_top" @click="ToPersonList">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-wangluoguanxitu"></use>
      </svg>
      <span></span>
    </div>
    <div id="show_graph" style=" height: 250px;"></div>
  </div>
</template>

<script>
export default {
  name: "relation_graph",
  data() {
    return {
      categories: [
        {
          name: "   ",
          color: "#00FFFF",
        },
        // {
        //   name: "",
        // },
      ],
    };
  },
  methods: {
    myRelationGraph(id) {
      let option;
      let myChart = this.$echarts.init(document.getElementById("show_graph"));
      myChart.showLoading();
      this.$axios.get("relation_graph?tag_task_id=" + id).then((res) => {
        console.log(res.data.data);
        let nodes = res.data.data.nodes_list;
        for (let index in nodes) {
          nodes[index].id = index;
        }
        let links = res.data.data.links_list;
        links.forEach((link) => {
          for (let node of nodes) {
            if (link.source == node.name) {
              link.source = node.id;
              node.show = true;
            }
            if (link.target == node.name) {
              link.target = node.id;
              node.show = true;
            }
          }
        });
        let newNodes = [];
        for (let n in nodes) {
          if (nodes[n].show) {
            newNodes.push(nodes[n]);
          }
        }
        console.log(newNodes);
        console.log(links);
        myChart.hideLoading();
        nodes.forEach(function (node) {
          node.label = {
            show: node.value > 10,
            color: '#ffffff',
          };
        });
        option = {
          // title: {
          //   text: "",
          //   top: "top",
          //   left: "left",
          // },
          tooltip: {},
          legend: [
            {
              data: this.categories.map(function (a) {
                return a.name;
              }),
            },
          ],
          animationDuration: 1500,
          animationEasingUpdate: "quinticInOut",
          series: [
            {
              name: "",
              type: "graph",
              layout: "force",
              data: newNodes,
              links: links,
              categories: this.categories,
              roam: true,
              label: {
                position: "right",
                formatter: "{b}",
                color: "#ffffff", // 
              },
              lineStyle: {
                color: "#00FFFF", // 
                curveness: 0.3,
              },
              emphasis: {
                focus: "adjacency",
                lineStyle: {
                  width: 10,
                  color: "#00FFFF",
                },
                nodeStyle: {
                  color: "#00FFFF", // 
                  borderWidth: 3,
                  shadowBlur: 20,
                  shadowColor: 'rgba(0,0,0,0.3)', // 
                },
                itemStyle: {
                color: "#00FFFF", // 
              },
              },
            },
          ],
        };
        myChart.setOption(option);
      });
      option && myChart.setOption(option);
    },
    ToPersonList() {
      this.$router.push({
        path: "/person_list",
        query: {
          tag_task_id: this.tid,
        },
      });
    },
  },
  mounted() {
    let res = Array.from(new Array(5), () => []);
    console.log(res);
    this.$bus.$on("send_tag_task_id", (tag_task_id) => {
      console.log(",:", tag_task_id);
      this.tid = tag_task_id;
      this.myRelationGraph(tag_task_id);
    });
  },
  beforeDestroy() {
    this.$bus.$off("send_tag_task_id");
  },
};
</script>

<style scoped>
.relation_graph_top {
  margin-top: 0px;
}

.relation_graph {
  border: 2px solid #fefeff6a; /*  */
  border-radius: 10px;
  background-color:rgba(254, 254, 255, 0);
  position: relative;

  width: 100%;
  height: 100%;
  /* margin-bottom: 5px; */
  cursor: pointer; /*  */
  color: #ffffff;
  border: 2px solid #669ef3a5; /*  */
  border-radius: 10px;
  
  box-sizing: border-box;
  background:rgba(52, 110, 173, 0); /*  */
  box-shadow: 0 0 15px rgba(233, 229, 238, 0.304), 0 0 25px rgba(63, 154, 87, 0.4); /*  */
}

.relation_graph:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /*  */
}

#show_graph {
  height: 90%;
  width: 90%;
  
}
</style>
