<template>
    <div class="chart-container">
      <!--  -->
      <div class="chart-box">
        <h3 class="chart-title"></h3>
        <div id="mood_pie_chart" class="chart"></div>
      </div>
  
      <!--  -->
      <div class="chart-box">
        <h3 class="chart-title"></h3>
        <div id="mood_line_chart" class="chart"></div>
      </div>
    </div>
  </template>
  
  
  
  <script>
  export default {
    name: "MoodAnalysis",
    data() {
      return {
        moodData: {}, // 
        myChartPie: null, // 
        myChartLine: null, // 
      };
    },
    methods: {
      // 
      getMoodData(tag_task_id) {
        this.$axios.get(`/mood_wx?tag_task_id=${tag_task_id}`).then((res) => {
          const data = res.data.data.data; // 
          this.moodData = data;
        console.log(data);
          // 
          const sentimentCounts = data.total_sentiment;
          const pieData = [
            { value: sentimentCounts.positive, name: "" },
            { value: sentimentCounts.neutral, name: "" },
            { value: sentimentCounts.negative, name: "" }
          ];
  
          this.drawPieChart(pieData);
  
          // 
          const sentimentByDate = data.sentiment_by_date;
          const dates = Object.keys(sentimentByDate);
          const positiveCounts = dates.map(date => sentimentByDate[date].positive);
          const neutralCounts = dates.map(date => sentimentByDate[date].neutral);
          const negativeCounts = dates.map(date => sentimentByDate[date].negative);
  
          const lineData = {
            dates,
            positive: positiveCounts,
            neutral: neutralCounts,
            negative: negativeCounts
          };
  
          this.drawLineChart(lineData);
        });
      },
  
      // 
        drawPieChart(data) {
        if (this.myChartPie) {
            this.myChartPie.dispose();
        }
        this.myChartPie = this.$echarts.init(document.getElementById("mood_pie_chart"));

        const option = {
            tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b} : {c} ({d}%)'
            },
            legend: {
            orient: 'vertical',
            left: 'left',
            data: ['', '', ''],
            textStyle: {
                color: '#fff' // 
            }
            },
            series: [
            {
                name: '',
                type: 'pie',
                radius: '50%',
                data: data,
                emphasis: {
                itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
                },
                label: {
                normal: {
                    show: false,
                    position: 'center',
                    formatter: '{b}\n{d}%',
                    // textStyle: {
                    // fontSize: 16,
                    // fontWeight: 'bold',
                    // color: '#4a4a4a'
                    // }
                }
                }
            }
            ]
        };
        this.myChartPie.setOption(option);
        },
        
      // 
      drawLineChart(data) {
        if (this.myChartLine) {
          this.myChartLine.dispose();
        }
        this.myChartLine = this.$echarts.init(document.getElementById("mood_line_chart"));
  
        const option = {
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: [ '','', ''],
            textStyle: {
                color: '#fff' // 
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: data.dates,
            axisLabel: {
                color: "white", // 
            },
          },
          yAxis: {
            type: 'value',
            axisLabel: {
                color: "white", // 
            },
          },
          series: [
            {
              name: '',
              type: 'line',
              data: data.negative,
              areaStyle: {}
            },
            {
              name: '',
              type: 'line',
              data: data.positive,
              areaStyle: {}
            },
            {
              name: '',
              type: 'line',
              data: data.neutral,
              areaStyle: {}
            },
            
          ]
        };
        this.myChartLine.setOption(option);
      }
    },
    mounted() {
    this.$bus.$on("send_tag_task_id", (tag_task_id) => {
      console.log("ID:", tag_task_id);
      this.getMoodData(tag_task_id); // 
    });
    },
    beforeDestroy() {
        this.$bus.$off("send_tag_task_id");
      if (this.myChartPie) {
        this.myChartPie.dispose();
      }
      if (this.myChartLine) {
        this.myChartLine.dispose();
      }
    }
  };
  </script>
  
  <style scoped>
 /*  */
.chart-container {
  width: 100%;
  height:62vh;
  margin-top: -120px;
  
  position: relative;
  font-family: Arial, sans-serif;
  background-color: #f9f9f900;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  
  box-sizing: border-box; /*  padding  */
  color: white; /*  */
}

/*  */
.chart-box {
    
  position: relative;
  width: 100%;
  height: 100%;
  border: 2px solid #4aaafac3; /*  */
  border-radius: 10px;
  box-shadow: 0 0 15px rgba(233, 229, 238, 0.304), 0 0 25px rgba(63, 154, 87, 0.4); /*  */
  margin-bottom: 10px;
}

/*  */
.chart-title {
  position: absolute;
  top: 0px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 13px;
  font-weight: bold;
  text-align: center;
  background-color: #3e3e3e00; 
  padding: 0px;
  color: #fff; /*  */
  z-index: 1; /*  */
}

/*  */
.chart {
  margin-top: 27px;
  width: 100%;
  height: 90%;
  background-color: #ffffff00;
  border-radius: 8px;
}

/*  */
@media screen and (max-width: 768px) {
  .chart-container {
    padding: 10px; /*  */
  }
  
  .chart-box {
    height: 250px !important; /*  */
  }
}

@media screen and (min-width: 769px) {
  .chart-container {
    
  }

  .chart-box {
    height: 100% !important; /*  */
  }
}

  </style>