<template>
  <div class="word_cloud" style="height: 38%;">
    <div class="word_cloud_top">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-ciyuntu"></use>
      </svg>
      <span class="title_text">  </span>
    </div>
    <div class="show_words">
      <div id="chars_word_cloud" style=" width: 350px; height:220px ;"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "word_cloud",
  data() {
    return {
      timer: "",
      myChart: "",
      cloud_word: "",
    };
  },
  methods: {
    getCloudWord(id) {
      this.$axios.get("word_cloud?tag_task_id=" + id).then((res) => {
        console.log(res.data)
        this.cloud_word = res.data.data.data;
        this.myWordCloud(res.data.data.data);
      });
    },
    myWordCloud(data) {
      let option;
      if (
        this.myChart != null &&
        this.myChart != "" &&
        this.myChart != undefined
      ) {
        this.myChart.dispose(); //echarts dom
      }
      this.myChart = this.$echarts.init(
        document.getElementById("chars_word_cloud")
      );
      // 
      option = {
        tooltip: {
          show: true,
        },
        series: [
          {
            type: "wordCloud",
            sizeRange: [10, 50], //
            //rotationStep45[-90,90]
            rotationRange: [-45, 90],
            rotationStep: 45,
            textRotation: [0, 45, 90, -45],
            //
            textStyle: {
              color: function () {
                //
                return (
                  "rgb(" +
                  [
                  Math.round(Math.random() * 55 + 200), // 200-255
                  Math.round(Math.random() * 55 + 200), // 200-255
                  Math.round(Math.random() * 55 + 200), // 200-255
                  ].join(",") +
                  ")"
                );
              },
            },
            data: data,
          },
        ],
      };
      this.myChart.setOption(option);
      // 
      option && this.myChart.setOption(option);
    },
  },
  mounted() {

    this.$bus.$on("send_tag_task_id", (tag_task_id) => {
      console.log(",:", tag_task_id);
      this.getCloudWord(tag_task_id);
    });
    this.timer = setInterval(() => {
      this.myWordCloud(this.cloud_word);
    }, 5000);
  },
  beforeDestroy() {
    this.$bus.$off("send_tag_task_id");
    clearInterval(this.timer);
    this.timer = null;
  },
};
</script>

<style scpoed>
.word_cloud {
  border: 2px solid #fefeff6a; /*  */
  border-radius: 10px;
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #ffffff00;
  color: #ffffff;
  border: 2px solid #669ef3a5; /*  */
  border-radius: 10px; 
  box-sizing: border-box;
  background: #346ead63; /*  */
  box-shadow: 0 0 15px rgba(233, 229, 238, 0.304), 0 0 25px rgba(63, 154, 87, 0.4); /*  */
}
.word_cloud_top {
  margin-left: 1px;
}
.w_title {
  padding-top: 10px;
  height: 50px;
  text-align: center;
  font-size: 20px;
}
.show_words {
  width: 90%;
  height: 90%;
}
#chars_word_cloud {
  width: 330px;
  height: 150px;
  top: -10px;
  position: relative;
  margin-left: -10px;
}
.icon {
  position: relative;
  top: 8px;
}
</style>